/* bmm styles */
.bmm-menu { width: 100%; position: relative; }

.bmm-menu:after { content: ""; display: table; clear: both; }

.bmm-menu, .bmm-sub-menu { list-style-type: none; margin: 0; padding: 0; }

.bmm-sub-menu { position: absolute; width: 100%; z-index: 999; left: 0; background: #fff; padding-top: 0.5rem; }

.bmm-primary-nav-item { float: left; }

.bmm-primary-nav-item .bmm-menu-link { padding: 5px 10px; display: inline-block; width: 100%; }

/* .column-menu-item > .pmm-mega-menu-item.pmm-mega-menu-item-has-children { position: relative; } */
/* transitions so that menus display for a bit after hover off */
.menu-item.bmm-primary-nav-item > .bmm-sub-menu { visibility: hidden; opacity: 0; -webkit-transition: visibility 0s linear 200ms, opacity 200ms; transition: visibility 0s linear 200ms, opacity 200ms; }

.menu-item:hover > .bmm-sub-menu { visibility: visible; opacity: 1; -webkit-transition: visibility 0s linear 0s, opacity 200ms; transition: visibility 0s linear 0s, opacity 200ms; }

/* .pmm-mega-menu-item.has-tabpane:hover>.pmm-mega-sub-menu.pmm-mega-sub-menu-tabpane  { transition-delay: 0s; } */
.column-menu-item { position: relative; width: 100%; float: left; padding-right: 2%; /* & > a.bmm-menu-link { padding: 0; } */ /* &:before { //content: ""; //display: block; } &:after { content: ""; display: table; clear: both; }  */ }

.column-menu-item.total-columns-2 { width: 49%; }

.column-menu-item.total-columns-3 { width: 32%; }

.column-menu-item.total-columns-4 { width: 23.5%; }

.column-menu-item.total-columns-5 { width: 18.4%; }

.column-menu-item.total-columns-6 { width: 15%; }

.column-menu-item.total-columns-7 { width: 12.571428%; }

.column-menu-item.total-columns-8 { width: 10.75%; }

.column-menu-item:last-child { padding-right: 0; }

